import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {AuthenticationProvider} from "../../providers/authentication/authentication";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";
import {ConfigProvider} from "../../providers/config/config";


@IonicPage()
@Component({
  selector: 'page-password-expire',
  templateUrl: 'password-expire.html',
})
export class PasswordExpirePage {

  passPolicy;
  changePassError = "";
  pass = "";
  confirmPass = "";
  version;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public maskTokenUtil: MaskTokenProvider,
              public storage: LocalStorageProvider,
              public alertCtrl: AlertController,
              public authenticationService: AuthenticationProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public tokenService: TokenServiceProvider,
              public gaProvider: GoogleAnalyticsProvider,
              public config : ConfigProvider) {


    this.version=this.config.iSmartVersion;
    this.passPolicy = this.storage.retrieveFromStorage("passPolicy");
  }

  ChangeExpirePassWdAction = function (passWd, confirmPassWd) {

    this.changePassError = "";
    this.pass = passWd;
    this.confirmPass = confirmPassWd;
    let flag = true;

    if (passWd == "" || typeof(passWd) == "undefined") {
      this.changePassError = "<li>Please Enter New Password."
      flag = false;
      return;
    }

    if (confirmPassWd == "" || typeof(confirmPassWd) == "undefined") {
      this.changePassError = "<li>Please Enter Confirm Password."
      flag = false;
      return;
    }

    if (!(passWd == confirmPassWd)) {
      this.changePassError = "<li>Password and Confirm Password Mismatch."
      flag = false;
      this.passWd = "";
      this.confirmPassWd = "";
      return;
    }

    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encNewPass = this.cryptoUtil.getEncryptVal(this.pass);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.authenticationService.changePassWd(unMaskToken, appData.userId, appData.appId, encNewPass)
        .then(data => {
          this.onExpirePassChangeComplete(data);
        });
    }
  };

  onExpirePassChangeComplete = function (data) {
    if (data.statusCode == 1) {
      this.navCtrl.setRoot('WelcomePage', {});
    }

    else if (data.statusCode == 0) {
      this.changePassError = data.note;
      this.passWd = "";
      this.confirmPassWd = "";
    }

    else {
      this.changePassError = "System Error Contact with Administrator.";
    }
  };

  showPasswordPolicy = function () {

    let radioAlert = this.alertCtrl.create();
    radioAlert.setTitle('Password Policy');
    radioAlert.setCssClass("alertLogCss");
    radioAlert.setMessage(this.passPolicy);
    radioAlert.addButton({
      text: 'OK',
      handler: data => {

      }
    });
    radioAlert.present();
    setTimeout(() => {
      radioAlert.dismiss();
    }, 30000);
  };

  ionViewDidLoad() {
    // console.log('ionViewDidLoad PasswordExpirePage');
    this.gaProvider.startTrackerWithId("Password Expire");
  }

}



